@charset "UTF-8";
/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f8f9fa; }

/* Skip Link for Accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: black;
  color: white;
  padding: 8px 12px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 1000;
  font-weight: 600;
  transition: top 0.3s ease; }
  .skip-link:focus {
    top: 6px;
    outline: 3px solid #3498db;
    outline-offset: 2px;
    box-shadow: 0 0 0 1px white; }

/* Additional Skip Links */
.skip-links {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: -100px;
  left: 0;
  z-index: 1000; }
  .skip-links:focus-within {
    top: 0; }
  .skip-links li {
    display: inline-block;
    margin-right: 0.5rem; }
  .skip-links a {
    display: block;
    background: black;
    color: white;
    padding: 8px 12px;
    text-decoration: none;
    border-radius: 0 0 4px 4px;
    font-weight: 600; }
    .skip-links a:focus {
      outline: 3px solid #3498db;
      outline-offset: 2px;
      box-shadow: 0 0 0 1px white; }

/* Main Grid Container */
.grid-container {
  display: grid;
  grid-template-areas: "topbar" "header" "navbar" "content" "footer";
  grid-template-rows: auto auto auto 1fr auto;
  min-height: 100vh;
  background: white; }

/* Top Bar Styles */
.top-bar {
  grid-area: topbar;
  background: black;
  color: white;
  padding: 0.5rem 0;
  position: relative;
  z-index: 1000;
  width: 100vw;
  margin-left: calc(50% - 50vw); }

.top-bar-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 40px; }
  .top-bar-content .top-bar-left,
  .top-bar-content .top-bar-right {
    display: flex;
    align-items: center;
    gap: 1rem;
    line-height: 0; }
  .top-bar-content .uw-logo-link {
    display: inline-block;
    transition: opacity 0.3s ease;
    line-height: 0; }
    .top-bar-content .uw-logo-link:hover, .top-bar-content .uw-logo-link:focus {
      opacity: 0.8; }
    .top-bar-content .uw-logo-link svg {
      height: 50px;
      width: auto;
      display: block;
      margin: 0;
      padding: 0; }
  .top-bar-content a {
    color: white;
    text-decoration: none;
    font-size: 0.9rem;
    transition: opacity 0.3s ease; }
    .top-bar-content a:hover, .top-bar-content a:focus {
      opacity: 0.8;
      text-decoration: underline; }

/* Header Styles */
.header {
  grid-area: header;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 2rem 0; }

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem; }
  .header-content h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    font-weight: 700; }
  .header-content p {
    font-size: 1.1rem;
    opacity: 0.9; }

.uw-links {
  background-color: #000;
  margin: 0 auto;
  max-width: 1200px; }

/* Navigation Styles */
.navbar {
  grid-area: navbar;
  background: #2c3e50;
  position: relative;
  padding: 0; }

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  max-width: 1200px;
  margin: 0 auto; }

.nav-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex; }
  .nav-menu li {
    position: relative; }
  .nav-menu a {
    display: block;
    color: white;
    text-decoration: none;
    padding: 1rem 1.5rem 1rem 0;
    transition: background-color 0.3s ease; }
    .nav-menu a:hover, .nav-menu a:focus {
      background-color: #34495e;
      outline: 3px solid #3498db;
      outline-offset: 2px;
      box-shadow: 0 0 0 1px white;
      outline-offset: -2px; }

/* Dropdown Styles */
.has-dropdown {
  position: relative; }
  .has-dropdown > a::after {
    content: ' ▼';
    font-size: 0.8em;
    margin-left: 0.5rem;
    transition: transform 0.3s ease; }
  .has-dropdown.open > a::after {
    transform: rotate(180deg); }

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: #34495e;
  min-width: 200px;
  list-style: none;
  margin: 0;
  padding: 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  display: none;
  border-radius: 0 0 4px 4px; }
  .dropdown li {
    border-bottom: 1px solid #2c3e50; }
    .dropdown li:last-child {
      border-bottom: none; }
  .dropdown a {
    padding: 0.75rem 1rem;
    font-size: 0.9rem; }
    .dropdown a:hover, .dropdown a:focus {
      background-color: #3498db; }

.has-dropdown.open .dropdown {
  display: block; }

/* Search Functionality */
.search-toggle {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  color: white;
  background: transparent;
  transition: background-color 0.3s ease;
  border-radius: 4px; }
  .search-toggle:hover, .search-toggle:focus {
    background-color: #34495e;
    outline: 3px solid #3498db;
    outline-offset: 2px;
    box-shadow: 0 0 0 1px white;
    outline-offset: -2px; }
  .search-toggle .dashicons {
    font-size: 1.2rem;
    line-height: 1; }

.search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  /* Trap focus within overlay */ }
  .search-overlay[aria-hidden="false"] {
    opacity: 1;
    visibility: visible; }
  .search-overlay:focus-within {
    outline: none; }

.search-container {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  position: relative;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3); }
  .search-container h2 {
    color: #2c3e50;
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
    text-align: center; }

.search-close {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem;
  color: #bdc3c7;
  transition: color 0.3s ease; }
  .search-close:hover, .search-close:focus {
    color: #2c3e50;
    outline: 3px solid #3498db;
    outline-offset: 2px;
    box-shadow: 0 0 0 1px white; }
  .search-close .dashicons {
    font-size: 1.5rem;
    line-height: 1; }

.search-form {
  display: flex;
  gap: 0.5rem;
  margin-top: 0; }

.search-field {
  flex: 1;
  padding: 1rem;
  border: 2px solid #bdc3c7;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s ease; }
  .search-field:focus {
    border-color: #3498db;
    outline: 3px solid #3498db;
    outline-offset: 2px;
    box-shadow: 0 0 0 1px white;
    outline-offset: -2px; }
  .search-field::placeholder {
    color: #bdc3c7; }

.search-submit {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  background: #3498db;
  color: white;
  padding: 1rem 1.5rem;
  border-radius: 4px;
  transition: background-color 0.3s ease; }
  .search-submit:hover, .search-submit:focus {
    background: #2980b9;
    outline: 3px solid #3498db;
    outline-offset: 2px;
    box-shadow: 0 0 0 1px white; }
  .search-submit .dashicons {
    font-size: 1rem;
    line-height: 1; }

.screen-reader-text {
  position: absolute !important;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0; }

/* Mobile Menu Toggle */
.mobile-menu-toggle {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  display: none;
  flex-direction: column;
  padding: 0.5rem; }
  .mobile-menu-toggle span {
    width: 25px;
    height: 3px;
    background: white;
    margin: 3px 0;
    transition: 0.3s ease;
    border-radius: 2px; }

/* Content Area Grid */
.content-area {
  grid-area: content;
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-areas: "nav main";
  min-height: 600px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem 0 0;
  gap: 1.5rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); }

/* Main Content Area */
.main {
  grid-area: main;
  padding: 2rem;
  background: white; }

/* Article Content */
.main-content,
.main > article,
.main > .content {
  width: 100%; }

/* Secondary Navigation */
.nav {
  grid-area: nav;
  background: #ecf0f1;
  padding: 1.5rem 0 1.5rem 1.5rem;
  border-radius: 4px; }
  .nav h2 {
    color: #2c3e50;
    margin-bottom: 1rem;
    font-size: 1.3rem;
    padding: 0 1rem 0.5rem 0;
    border-bottom: 2px solid #3498db; }
  .nav ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .nav ul li {
      position: relative; }
      .nav ul li a {
        display: block;
        color: #333;
        text-decoration: none;
        padding: 0.5rem 0 0.5rem 1rem;
        transition: all 0.3s ease;
        border-left: 3px solid transparent; }
        .nav ul li a:hover, .nav ul li a:focus {
          background-color: #ecf0f1;
          border-left-color: #3498db;
          outline: 3px solid #3498db;
          outline-offset: 2px;
          box-shadow: 0 0 0 1px white;
          outline-offset: -2px; }
      .nav ul li.current-page > a {
        background-color: #3498db;
        color: white;
        border-left-color: #2980b9;
        font-weight: 600; }
      .nav ul li.current-ancestor > a {
        background-color: #ecf0f1;
        border-left-color: #bdc3c7;
        font-weight: 500; }
      .nav ul li.has-children > a {
        position: relative; }
        .nav ul li.has-children > a::after {
          content: '▼';
          position: absolute;
          right: 1rem;
          font-size: 0.8em;
          color: #bdc3c7; }
    .nav ul .sub-menu {
      margin-left: 1rem;
      border-left: 1px solid #bdc3c7; }
      .nav ul .sub-menu li a {
        padding-left: 1.5rem;
        font-size: 0.9rem;
        color: #555; }
        .nav ul .sub-menu li a:hover, .nav ul .sub-menu li a:focus {
          color: #333; }
      .nav ul .sub-menu.level-1 li a {
        padding-left: calc(1.5rem + 0.5rem); }
      .nav ul .sub-menu.level-2 li a {
        padding-left: calc(1.5rem + 1rem);
        font-size: 0.85rem; }
      .nav ul .sub-menu.level-3 li a {
        padding-left: calc(1.5rem + 1.5rem);
        font-size: 0.8rem; }

/* Main Content Styles */
.main-content {
  background: white; }
  .main-content h1 {
    color: #2c3e50;
    margin-bottom: 1rem;
    font-size: 2rem;
    line-height: 1.2; }
  .main-content h2 {
    color: #34495e;
    margin-top: 2rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
    line-height: 1.3; }
  .main-content p {
    margin-bottom: 1rem;
    color: #555;
    line-height: 1.7;
    max-width: 70ch;
    /* Improve readability */ }
  .main-content article {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #ecf0f1; }
    .main-content article:last-child {
      border-bottom: none;
      margin-bottom: 0; }

/* Footer Styles */
.footer {
  grid-area: footer;
  background: #2c3e50;
  color: white;
  padding: 2rem 0; }

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  text-align: center; }
  .footer-content p {
    margin-bottom: 0.5rem;
    opacity: 0.9; }
    .footer-content p:last-child {
      margin-bottom: 0;
      font-size: 0.9rem;
      opacity: 0.7; }

/* Responsive Design */
@media (max-width: 768px) {
  .content-area {
    grid-template-areas: "nav"  "main";
    gap: 1rem;
    margin: 0;
    box-shadow: none; }
  .main {
    padding: 1.5rem; }
  .nav {
    margin-bottom: 1rem; }
  .nav-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #2c3e50;
    flex-direction: column;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); }
    .nav-menu.active {
      display: flex; }
    .nav-menu a {
      border-bottom: 1px solid #34495e; }
      .nav-menu a:last-child {
        border-bottom: none; }
  .mobile-menu-toggle {
    display: flex; }
    .mobile-menu-toggle[aria-expanded="true"] span:nth-child(1) {
      transform: rotate(45deg) translate(5px, 5px); }
    .mobile-menu-toggle[aria-expanded="true"] span:nth-child(2) {
      opacity: 0; }
    .mobile-menu-toggle[aria-expanded="true"] span:nth-child(3) {
      transform: rotate(-45deg) translate(7px, -6px); }
  .search-container {
    padding: 1.5rem;
    margin: 0 1rem;
    width: calc(100% - 1.5rem); }
  .search-form {
    flex-direction: column;
    gap: 1rem; }
  .search-submit {
    align-self: flex-start; }
  .dropdown {
    position: static;
    box-shadow: none;
    background: #34495e;
    border-radius: 0; }
  .has-dropdown > a::after {
    content: ' ▶'; }
  .has-dropdown.open > a::after {
    content: ' ▼'; } }

@media (max-width: 480px) {
  .header {
    padding: 1.5rem 0; }
    .header .header-content h1 {
      font-size: 1.8rem; }
  .content-area {
    grid-template-areas: "nav" "main";
    margin: 0 1rem; }
  .main {
    padding: 1rem; }
  .main-content {
    padding: 1rem; }
    .main-content h1 {
      font-size: 1.6rem; }
    .main-content h2 {
      font-size: 1.3rem; }
    .main-content p {
      max-width: none;
      /* Full width on mobile */ }
  .nav {
    padding: 1rem; }
  .footer {
    padding: 1.5rem 0; } }

/* High Contrast and Accessibility */
@media (prefers-contrast: high) {
  .header {
    background: black;
    border-bottom: 2px solid white; }
  .navbar {
    background: black;
    border-bottom: 1px solid #bdc3c7; }
  .nav-menu a {
    border: 1px solid transparent; }
    .nav-menu a:hover, .nav-menu a:focus {
      background-color: white;
      color: black;
      border-color: black; }
  .nav {
    border: 2px solid #2c3e50; }
    .nav a {
      border: 1px solid transparent; }
      .nav a:hover, .nav a:focus {
        border-color: #3498db; }
  .search-container {
    border: 2px solid #2c3e50; } }

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important; } }

/* Focus Styles for Better Accessibility */
a:focus,
button:focus {
  outline: 3px solid #3498db;
  outline-offset: 2px;
  box-shadow: 0 0 0 1px white; }

/* Print Styles */
@media print {
  .navbar,
  .footer {
    display: none; }
  .grid-container {
    box-shadow: none; }
  .content-area {
    grid-template-columns: 1fr; }
  .main-content {
    padding: 0; } }
